import React, { Component } from 'react';

class ErrorBoundary extends Component {

  constructor(props){
      super(props)

      this.state = {
        hasError:false,
        ErrorMessage:''
      }
  }

  static getDerivedStateFromError(error){
      return{
          hasError:true
      }
  }

  componentDidCatch(error, info){
      let ErrMsg = "Error is "+error;
      this.setState({
          ErrorMessage: ErrMsg,
          hasError:true
      })
      console.log("Error INfo"+info);
  }

  render() {
    if(this.state.hasError){
        console.log("boundary error occured");
        return (<h4 className="BoundaryErrMsg">{this.state.ErrorMessage}</h4>);
    }
    return this.props.children
  }
}

export default ErrorBoundary;



