import React, { Component } from 'react';
import {Link} from 'react-router-dom';

class Menu extends Component {

    constructor(props){
        super(props)
    }

    refresh(){
        window.history.go();
    }
    
    render() {
        return (
            <div >
                <div className="Menu-style">
                    <button style={{float: "left"}} type="button" className=" btn btn-light MenuButton" data-toggle="collapse" 
                                    data-target={"#CollapsableMenu"}>
                        <div className="menu"></div>
                        <div className="menu"></div>
                        <div className="menu"></div>
                    </button> 
                        {' '}{" "}
                        
                    <Link to ="/">
                    <a style={{marginleft: "10%", display:"block"}}>Finding Falcone !! ...</a>
                    </Link>
                    <Link style={{float: "right"}} >
                    <a onClick={this.refresh}>Reset</a>
                    </Link>
                    <Link to ="/" style={{float: "right"}}>
                    <a href="#" >GeekTrust Home</a>
                    </Link>

                </div>

                <div id="CollapsableMenu" className="collapse collapsemenu">
                    <div id="menuLinks" >
                        <Link to ="/">
                        <a href="#" >GeekTrust Home</a>
                        </Link>
                        <Link>
                        <a onClick={this.refresh}>Reset</a>
                        </Link>
                    </div>
                </div>

            </div>
        );
  }
}

export default Menu;


