import React, { Component } from 'react';

class Destination extends Component {

  constructor(props){
        super(props)
  }
  render() {
      return (
        
        <div className="Planet">
          <p>Destination {this.props.id}</p>

        <select name="planets" id={this.props.id} onChange={this.props.getSelectedPlanet} className="">
        <option value="select">Select</option>
        { 
          this.props.planetArr.map((planet)=>{
                return(
                        <option key={planet.distance} value={planet.name}>{planet.name}</option>
                )
            }
          )
        }
        </select><br></br>
        
        <div className="AvailableVehicles" id={this.props.AvailableVehicleId}>
          {
            this.props.vehicleArr.map((vehicle)=>{
                let maxDist = vehicle.max_distance;
                
                  
                  if(this.props.destinationId == this.props.id){
                    let planetDist = this.props.selectedPlanetsArr[this.props.selectedPlanetsArr.length-1].distance;
                    
                    if((maxDist >= planetDist)&&(vehicle.total_no>0)){
                      return(
                        <div key={vehicle.max_distance} >
                          <input type="radio"  id={vehicle.max_distance} name={this.props.AvailableVehicleId} value={vehicle.name} 
                            onChange={this.props.getSelectShip}/>{" "}
                          <label for={vehicle.name}>{vehicle.name}{" "}{"("}{vehicle.total_no}{")"}</label>
                          <br></br>
                        </div>
                      )
                    }
                    else{
                      return(
                        <div key={vehicle.max_distance} >
                          <input type="radio"  id={vehicle.max_distance} name={this.props.AvailableVehicleId} value={vehicle.name} 
                            onChange={this.props.getSelectShip} disabled/>{" "}
                          <label for={vehicle.name}>{vehicle.name}{" "}{"("}{vehicle.total_no}{")"}</label>
                          <br></br>
                        </div>
                      )
                    }
                  }
                  else{
                    return(
                      <div key={vehicle.max_distance} >
                        <input type="radio"  id={vehicle.max_distance} name={this.props.AvailableVehicleId} value={vehicle.name} 
                          onChange={this.props.getSelectShip} disabled/>{" "}
                        <label for={vehicle.name}>{vehicle.name}{" "}{"("}{vehicle.total_no}{")"}</label>
                        <br></br>
                      </div>
                    )
                  }
                  
              }
              
            )

          }
        </div>
        </div>
      );
  }
}

export default Destination;


