import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import { Redirect } from 'react-router-dom';
import Menu from './Menu';
import Footer from './Footer';
import Destination from './Destination';
import Loading from './Loading';
import ErrorBoundary from './ErrorBoundary';

class Home extends Component {

  constructor(props){
    super(props)

    this.state = {
        redirect: false,
        planetArr: [],
        vehicleArr:[],
        selectedPlanetsArr: [],
        UsedVehicle:[],
        destinationId:0,
        Time: 0,
        message: 'Loading Please wait...',
        Error: ''
    }
  }

  componentDidMount(){
      this.getData();
  }

  async getData(){

    try{

    let planetList = [],vehicleList=[];

    fetch('https://findfalcone.herokuapp.com/planets')
    .then(response => response.json())
    .then(planetdata => {
      //console.log('Success:', planetdata);
      for(let i=0;i<4;i++){
        planetList.push(planetdata);
      }

      this.setState({
        planetArr:[...planetList],
      })

    })
    .catch((error) => {
      console.log('LoadError:', error);
      this.setState({
        message: "Unable to Load, please try again.."
      })
    });

    fetch('https://findfalcone.herokuapp.com/vehicles')
    .then(response => response.json())
    .then(vehicledata => {
      //console.log('vehicledata is:', vehicledata);
      for(let i=0;i<4;i++){
        vehicleList.push(vehicledata);
      }

      this.setState({
        vehicleArr:[...vehicleList],
      })

    })
    .catch((error) => {
      console.error('Error:', error);
      this.setState({
        message: "Unable to Load, please try again.."
      })
    });
    
    }catch(err){
      let errmsg = "Error is "+err;
      this.setState({
        Error: errmsg,
        message: errmsg
      })
    }
  }

  async getFindFalcone(){

    try{

      let destinations = [], maindata = {}, falconeresult={};;

      const token = await fetch('https://findfalcone.herokuapp.com/token', {
        method: 'POST', // or 'PUT'
        headers: {
          "Accept" : "application/json",
        }
      });

      const tokendata = await token.json();
      //console.log("Token is "+tokendata.token);
      
      for(let i=0; i<this.state.selectedPlanetsArr.length; i++){
          destinations.push(this.state.selectedPlanetsArr[i].name);
      }

      maindata = { "token": tokendata.token,
                        "planet_names": [...destinations],
                        "vehicle_names": [...this.state.UsedVehicle]
      }
                     
      fetch('https://findfalcone.herokuapp.com/find', {
        method: 'POST', // or 'PUT'
        headers: {
          "Accept" : "application/json",
          "Content-Type": "application/json",
        },
        body: JSON.stringify(maindata),
      })
      .then(response => response.json())
      .then(data => {
        falconeresult = data;
        if(falconeresult.status == "success"){
          
          let resultarr = ["","",""];
          let planetfound = "Planet found is : "+falconeresult.planet_name;
          
          resultarr[0] = "Time Taken is: "+ this.state.Time;
          resultarr[1] = "Success! Congratulations on finding Falcone, King Shan is mighty pleased";
          resultarr[2] = planetfound;

          localStorage.setItem('FinalResultArr',JSON.stringify(resultarr));
          this.setState({
            redirect: true
          })
        }
        else{
          
          let resultarr = ["","",""];
          resultarr[0] = "Time Taken is: "+ this.state.Time;
          resultarr[1] = "Sorry! Falcone not found";
          resultarr[2] = "";

          localStorage.setItem('FinalResultArr',JSON.stringify(resultarr));
          this.setState({
            redirect: true
          })
        }
        
      })
      .catch((error) => {
          console.error('ErrorFalcone:', error);
          let errmsg = "Error is "+error;
          this.setState({
            redirect: true,
            Error: errmsg,
            message: errmsg
          })
      });
    }catch(err){
      let errmsg = "Error is "+err;
      this.setState({
        Error: errmsg,
        message: errmsg,
        redirect: true
      })
    }
  }

  getSelectedPlanet = (event) => {

  try{

    let destinationId = event.target.id, planets;
    let planetname = event.target.value,position;
    let selectedPlanet = [...this.state.selectedPlanetsArr];

      for(let i=0;i<this.state.planetArr.length;i++){
        if(destinationId == (i+1)){
          planets = [...this.state.planetArr[i]];
          break;
        }
      }

      //console.log('NewPlanetArr is :', this.state.planetArr[0]);
      //console.log('NewVehilceArr is :', this.state.vehicleArr);

      for (let i=0; i<planets.length; i++){
        if(planets[i].name == planetname){
            position=i;
            break;
        }
      }

    selectedPlanet.splice(selectedPlanet.length, 0,planets[position]);
    planets.splice(position, 1);

    //console.log("selectedPlanet distance is "+selectedPlanet[selectedPlanet.length-1].distance);

    if(destinationId == 1){
      this.setState({
        planetArr:[this.state.planetArr[0],planets,planets,planets],
        selectedPlanetsArr:selectedPlanet,
        destinationId: destinationId
      });
     }
     else if(destinationId == 2){
      this.setState({
        planetArr:[this.state.planetArr[0],this.state.planetArr[1],planets,planets],
        selectedPlanetsArr:selectedPlanet,
        destinationId: destinationId
      });
     }
     else if(destinationId == 3){
      this.setState({
        planetArr:[this.state.planetArr[0],this.state.planetArr[1],this.state.planetArr[2],planets],
        selectedPlanetsArr:selectedPlanet,
        destinationId: destinationId
      });
     }
     else{
      this.setState({
        selectedPlanetsArr:selectedPlanet,
        destinationId: destinationId
      });
     }

    //
    document.getElementById(destinationId).disabled = true;

     for(let i=0;i<this.state.planetArr.length;i++)
     {
       if(destinationId == (i+1)){
        let x = document.getElementById("Vehicle"+(i+1));
        if (x.style.display === "none") {
          x.style.display = "none";
        } else {
          x.style.display = "block";
        }
       }
     }

    } catch(err){
      console.log("Select Planet Error is "+err);
      let errmsg = "Select Planet Error is "+err;
      this.setState({
        Error: errmsg,
        message: errmsg
      })
    }
  }

  getSelectShip = (event) => {
    try{

    let shipname = event.target.value,planetdistance,time;
    let availableShipId = event.target.name, usedVehicleArr = [];
    let shipArr =[],position=0,name,total=0,max_distance=100,speed=2;

    for(let i=0;i<this.state.vehicleArr.length;i++){
      if(availableShipId == ("Vehicle"+(i+1))){
        shipArr = [...this.state.vehicleArr[i]]
      }
    }

    for (let i=0; i<shipArr.length; i++){
      if(shipArr[i].name == shipname){
         if(shipArr[i].total_no > 0){
            total = shipArr[i].total_no - 1;
            position=i;
            break;
         }
      }
    }

    name = shipArr[position].name;
    max_distance = shipArr[position].max_distance;
    speed = shipArr[position].speed;

    shipArr.splice(position,1);
    shipArr.splice(position,0, {"name":name,"total_no":total,"max_distance":max_distance,"speed":speed});
    //
    usedVehicleArr=[...this.state.UsedVehicle];
    usedVehicleArr.push(name);
    //
    time = this.state.Time;
    planetdistance = this.state.selectedPlanetsArr[this.state.selectedPlanetsArr.length-1].distance;
    time = time + (planetdistance/speed);

    this.setState({
      UsedVehicle: usedVehicleArr,
      Time: time
    })

        if(availableShipId == "Vehicle1"){
          this.setState({
            vehicleArr:[shipArr,shipArr,this.state.vehicleArr[2],this.state.vehicleArr[3]]
          });
        }
        else if(availableShipId == "Vehicle2"){
          this.setState({
            vehicleArr:[this.state.vehicleArr[0],shipArr,shipArr,this.state.vehicleArr[3]]
          });
        }
        else if(availableShipId == "Vehicle3"){
          this.setState({
            vehicleArr:[this.state.vehicleArr[0],this.state.vehicleArr[1],shipArr,shipArr]
          });
        }
        else{
          this.setState({
            vehicleArr:[this.state.vehicleArr[0],this.state.vehicleArr[1],this.state.vehicleArr[2],shipArr]
          });
        }

      /* this.setState({
        Sid: availableShipId
       });*/

       document.getElementById(availableShipId).disabled = true;
        var nodes = document.getElementById(availableShipId).getElementsByTagName('*');
        for(var i = 0; i < nodes.length; i++){
              nodes[i].disabled = true;
        }
      }catch(err){
          console.log("Select Ship Error is "+err);
          let errmsg = "on choosing ship error: "+err;
          this.setState({
            Error: errmsg,
            message: errmsg
          })
      }
  }

  renderRedirect = () => {
    try{
      if (this.state.redirect) {
        return <Redirect to='/result' />
      }
    }catch(err){
      let errmsg = "Error is "+err;
      this.setState({
        Error: errmsg,
        message: errmsg
      })
    }
  }
  
  render() {

    if((Array.isArray(this.state.planetArr[0])) && (Array.isArray(this.state.vehicleArr[0])) && (this.state.Error === ''))
    return (
      <div className="Root">
        <div className="MainContent">

          <ErrorBoundary>
          <Menu />
          </ErrorBoundary>

          <h4>Select Planets you want to search in:</h4>

          <div className="DestinationLists">

            <ErrorBoundary>
            <Destination planetArr={this.state.planetArr[0]} getSelectedPlanet={this.getSelectedPlanet} vehicleArr={this.state.vehicleArr[0]}
              destinationId={this.state.destinationId} selectedPlanetsArr={this.state.selectedPlanetsArr} AvailableVehicleId="Vehicle1" id="1" getSelectShip={this.getSelectShip}/>
            </ErrorBoundary>
            
            <ErrorBoundary>         
            <Destination planetArr={this.state.planetArr[1]} getSelectedPlanet={this.getSelectedPlanet} vehicleArr={this.state.vehicleArr[1]}
              destinationId={this.state.destinationId} selectedPlanetsArr={this.state.selectedPlanetsArr} AvailableVehicleId="Vehicle2" id="2" getSelectShip={this.getSelectShip}/>
            </ErrorBoundary>            

            <ErrorBoundary>            
            <Destination planetArr={this.state.planetArr[2]} getSelectedPlanet={this.getSelectedPlanet} vehicleArr={this.state.vehicleArr[2]}
              destinationId={this.state.destinationId} selectedPlanetsArr={this.state.selectedPlanetsArr} AvailableVehicleId="Vehicle3" id="3" getSelectShip={this.getSelectShip}/>
            </ErrorBoundary>            
            
            <ErrorBoundary>
            <Destination planetArr={this.state.planetArr[3]} getSelectedPlanet={this.getSelectedPlanet} vehicleArr={this.state.vehicleArr[3]}
              destinationId={this.state.destinationId} selectedPlanetsArr={this.state.selectedPlanetsArr} AvailableVehicleId="Vehicle4" id="4" getSelectShip={this.getSelectShip}/>
            </ErrorBoundary>            
          </div>
        
          <label className="Time">{"Total Time taken : "+this.state.Time}</label><br></br>
        
        
          {this.renderRedirect()}

          <button onClick={async () => {await this.getFindFalcone();}} className="btn btn-success ButtonFindFalcone"> 
              Find Falcone!
          </button>

        </div>

        <ErrorBoundary>
        <Footer />
        </ErrorBoundary>

      </div>
    )
    else{
      return(
        
        <Loading message={this.state.message}/>
        
      )
    }
    
  }
  
}

export default Home;


