import React, { Component } from 'react';
import Menu from './Menu';
import Footer from './Footer';
import ErrorBoundary from './ErrorBoundary';

class Result extends Component {

  render() {
       let arr = localStorage.getItem('FinalResultArr');
       let arrnew = JSON.parse(arr);
      
      return (
        <div className="Root">

                <div className="MainContent" style={{height: "670px"}}>

                <ErrorBoundary>
                <Menu />
                </ErrorBoundary>

                    <label className="ResultLabel" style={{margin: "20px"}}><strong>{arrnew[0]}</strong></label><br></br>

                    <label className="ResultLabel"><strong>{arrnew[1]}</strong></label><br></br>

                    <label className="ResultLabel"><strong>{arrnew[2]}</strong></label>
                </div>

            <ErrorBoundary>
            <Footer />
            </ErrorBoundary>

        </div>
      );
  }
}

export default Result;


