import React, { Component } from 'react';
import Menu from './Menu';
import Footer from './Footer';
import ErrorBoundary from './ErrorBoundary';

class Loading extends Component {

    constructor(props){
        super(props)
      }
  render() {
      return (
        <div className="Root">

                <div className="MainContent" style={{height: "670px"}}>

                    <ErrorBoundary>
                    <Menu />
                    </ErrorBoundary>

                    <br></br><br></br>
                    <h4>{this.props.message}</h4><br></br><br></br>
                </div>
            
            <ErrorBoundary>
            <Footer />
            </ErrorBoundary>
               
        </div>
      );
  }
}

export default Loading;


